import request from '../utils/request';

const get = id =>
  request({
    url: '/app/user/other-detail',
    method: 'get',
    params: {
      user_id: id,
    },
  });
const edit = form =>
  request({
    url: '/app/user/edit',
    method: 'post',
    data: {
      nickname: form.nickname,
      sex: form.sex,
    },
  });
export default {
  get,
  edit,
};



